import { Injectable } from '@angular/core';
import { Subject } from 'rxjs/Subject';
import { HttpHeaders } from '@angular/common/http';
import { IfObservable } from 'rxjs/observable/IfObservable';
import { Observable } from 'rxjs/Observable';
import { RestfulService } from '@farris/devkit';

const WORKSPACE = '/api/dev/main/v1.0/workspace';

@Injectable()
export class WorkspaceService {

  constructor(private httpClient: RestfulService) { }
  public sessionid: string;
  private getUrl() {
    return WORKSPACE;
  }
  public getWorkSpaces() {
    const headers = new HttpHeaders().set('SessionId', this.sessionid);
    const url = this.getUrl() + '/namespace';
    const workspaces = new Subject<any[]>();
    this.httpClient.get(url, null, { headers }).subscribe(
      result => {
        const res = [];
        result.forEach(element => {
          res.push({
            'ID': element.id,
            'Name': element.config.name,
            'Status': element.status,
            'StatusName': element.status === 1 ? '' : '',
            'AccountName': element.account.name,
            'Account': element.account,
            'Projects': element.config.projects
          });
        });
        console.log(res);
        if (res) {
          workspaces.next(res);
        }
      });
    return workspaces;
  }

  public startWorkSpace(id: string): Observable<any> {
    const headers = new HttpHeaders().set('SessionId', this.sessionid);
    const url = this.getUrl() + '/' + id + '/runtime';
    return this.httpClient.post(url, null, null, { headers });
  }

  public stopWorkSpace(id: string): Observable<any> {
    const headers = new HttpHeaders().set('SessionId', this.sessionid);
    const url = this.getUrl() + '/' + id + '/runtime';
    return this.httpClient.delete(url, null, { headers });
  }

  public createWorkSpace(body: any): Observable<any> {
    const headers = new HttpHeaders().set('SessionId', this.sessionid);
    const url = this.getUrl();
    return this.httpClient.post(url, body, null, { headers });
  }

  public delete(id: string): Observable<any> {
    const headers = new HttpHeaders().set('SessionId', this.sessionid);
    const url = this.getUrl() + '/' + id;
    return this.httpClient.delete(url, null, { headers });
  }
}



