import { Component, OnInit, ViewChild, Input } from '@angular/core';
import { RowArgs } from '@progress/kendo-angular-grid';
import { SelectableSettings, SelectableMode } from '@progress/kendo-angular-grid';
import { WorkspaceService } from '../workspace.service';
import { ParamMap, ActivatedRoute } from '@angular/router';
import { FrameworkService } from '@gsp-sys/rtf-common';

@Component({
  selector: 'app-workspacemgr',
  templateUrl: './workspacemgr.component.html',
  styleUrls: ['./workspacemgr.component.css']
})
export class WorkspacemgrComponent implements OnInit {
  constructor(private service: WorkspaceService, 
    private frmService: FrameworkService,
    private route: ActivatedRoute) {
      
      this.setSelectableSettings();
  }
  public checkboxOnly = false;
  public mode: SelectableMode = 'single';
  public selectableSettings: SelectableSettings;
  public position: 'top' | 'bottom' | 'both' = 'top';
  public mySelection = -1;
  public gridData: any[] = [
  ];
  public opened = false;
  public name = '';
  private selectedItem: any;
  //private sessionid: string;

  // buttons status
  public addFlag = false;
  public deleteFlag = true;
  public startFlag = true;
  public stopFlag = true;
  ngOnInit() {
    this.route.paramMap.subscribe((params: ParamMap) => {
      if (params.has('funcid')) {
        const funcid = params.get('funcid');
        this.service.sessionid = this.frmService.getCurrentSession(funcid);
        
        this.loadData();
      }
    });
  }
  public loadData(): void {
    this.service.getWorkSpaces().subscribe(result => {
      this.gridData = result;
    });
  }
  public onClickAdd(): void {
    this.opened = !this.opened;
    this.opened = true;
  }
  public onClickRemove(): void {
    if (this.mySelection < 0) {
      alert('');
      return;
    }
    this.service.delete(this.selectedItem.ID).subscribe(
      result => {
        this.gridData.splice(this.mySelection[0], 1);
        alert('.');
        this.setButtonsStatus();
      }
    );
  }
  public onClickStart(): void {
    if (this.mySelection < 0) {
      alert('');
      return;
    }
    this.service.startWorkSpace(this.selectedItem.ID).subscribe(
      result => {
        for (let i = 0; i < this.gridData.length; i++) {
          if (i === this.mySelection) {
            this.gridData[i].Status = 2;
            this.gridData[i].StatusName = '';
          } else {
            this.gridData[i].Status = 1;
            this.gridData[i].StatusName = '';
          }
        }
        alert('.');
        this.setButtonsStatus();
      }
    );
  }
  public onClickStop(): void {
    if (this.mySelection < 0) {
      alert('');
      return;
    }
    this.service.stopWorkSpace(this.selectedItem.ID).subscribe(
      result => {
        this.gridData[this.mySelection].Status = 1;
        this.gridData[this.mySelection].StatusName = '';
        alert('.');
        this.setButtonsStatus();
      }
    );
  }
  public mySelectionKey(context: RowArgs) {
    this.selectedItem = context.dataItem;
  }

  public onSelectedKeysChange(e) {
    if (e && e.selectedRows.length > 0) {
      this.mySelection = e.selectedRows[0].index;
      this.selectedItem = e.selectedRows[0].dataItem;
    } else {
      this.mySelection = -1;
      this.selectedItem = {};
    }
    this.setButtonsStatus();
  }

  private setButtonsStatus() {
    this.addFlag = false;
    this.deleteFlag = true;
    this.startFlag = true;
    this.stopFlag = true;
    if (this.mySelection < 0) {
      return;
    }
    this.deleteFlag = false;
    if (this.selectedItem.Status === 1) {
      this.startFlag = false;
    } else if (this.selectedItem.Status === 2) {
      this.stopFlag = false;
    }
  }

  public onClickHidden(): void {
    this.selectedItem = {};
    this.opened = !this.opened;
  }
  public onClickSubmit(): void {
    if (!this.name) {
      alert('');
      return;
    }
    const config = {
      'name': this.name
    };
    this.service.createWorkSpace(config).subscribe(
      element => {
        this.gridData.push({
          'ID': element.id,
          'Name': element.config.name,
          'Status': element.Status,
          'StatusName': element.status === 1 ? '' : '',
          'AccountName': element.account.name,
          'Account': element.account,
          'Projects': element.config.projects
        });
        alert('.');
        this.name = '';
        this.onClickHidden();
      }
    );
  }
  public onClickCancel(): void {
    this.onClickHidden();
  }
  public setSelectableSettings(): void {
    this.selectableSettings = {
      checkboxOnly: this.checkboxOnly,
      mode: this.mode,
      enabled: true
    };
  }
}



