import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { WorkspacemgrComponent } from './workspacemgr/workspacemgr.component';

const routes: Routes = [
    { path: '', component: WorkspacemgrComponent }
];

@NgModule({
  imports: [
    RouterModule.forChild(routes)
  ],
  exports: [
    RouterModule
  ]
})
class WorkspaceRoutingModule {}

export { WorkspaceRoutingModule };



